import React from 'react';
import ReactDOM from 'react-dom/client';

import './index.css';

import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';

import mainComponents from './components/index.js';

import ChartJS from './charts.js'

const root = ReactDOM.createRoot(document.getElementById('root'));

let App = () => {
  return (
    <div className="App">
      {mainComponents.map((x, i) => (
        <Accordion {...([0, mainComponents.length-1].includes(i) ? {defaultActiveKey: i} :  {})} key={i}>
          <Accordion.Item eventKey={i} className='control-box'>
            <Accordion.Header>{x.name}</Accordion.Header>
            <Accordion.Body>
              <Container className="applets">
                {x.usage ? x.usage() : null}
                {x.app()}
              </Container>
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
      ))}
      <footer className="text-center">{(new Date()).getFullYear()} © Jonathan Edmonson</footer>
    </div>
  );
}

root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
