import Greeting from './greet'
import { useEffect, useState } from 'react'


let HomeAbout = () => {
  let [popData, setPopData] = useState(null)
  useEffect(() => {
    fetch(
      'https://www.census.gov/popclock/data/population.php/world'
    ).then(
      res => res.json()
    ).then(
      setPopData
    ).catch(
      (err) => console.log(err)
    )
  }, [])
  // console.log(popData)
  return (
    <div id="centerpiece" style={{ flexDirection: "column" }} className="text-center">
      <h2><Greeting /></h2>
      <div id="blurb">
        <div id="headshot"></div>
        <h3>Hi! My name is Jonathan Edmonson.</h3>
        <p>
          I am 1 of <a rel="noreferrer" href="https://www.census.gov/popclock/" target="_blank" >{popData ? popData.world.population.toLocaleString() : null}</a>
        </p>
      </div>
      Interests:
      <ul>
        <li>STEM</li>
        <li>Security</li>
        <li>Finance</li>
        <li>Radio</li>
        <li>Software</li>
        <li>Hardware</li>
        <li>Networks</li>
      </ul>
      <br />
      <p>Below you can find a demonstration of some things I find helpful.</p>
    </div>
  )
}

export default HomeAbout;
